import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'department-demo-basic',
  template: `
    <h3>开单网点选择组件</h3>
    <!--复制开始-->
    <div style="margin: 15px 0">例如输入  一智通</div>
    <department-select width="200px" defaultLabel="请选择…" [(ngModel)]="departmentCode"></department-select>
    <!--复制结束-->
    <div style="margin: 15px 0">{{departmentCode}}</div>
    <br>

    <button (click)="doTest()">测试取Code</button>
  `,
  styles: []
})
export class DepartmentDemoBasicComponent implements OnInit {
  departmentCode: string = "";

  constructor() {
  }

  ngOnInit() {

  }

  doTest() {
    alert('值为：' + this.departmentCode);
  }

}



